body {
  text-align: center;
  background-color: slategrey;
  height: 100vh;
}

.App-logo {
  animation: App-logo-spin infinite 69s linear;
  height: 20vmin;
}

h1 {
  text-decoration: underline;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
